@import "./base.scss";
.coolLink{
    position: relative;
    display: grid;
    place-items: center;
    overflow: hidden;
    &:before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: currentColor;
        transform: scaleX(0);
        transform-origin: right;
        transition: transform 0.3s cubic-bezier(0.5, 0.5, 0.3, 1);
        color: $purple-electric;
      }
    &:after{
        content: attr(data-text);
        position: absolute;
        top: 0;
        height: 100%;
        transform: translate3d(150%,0,0);
        -webkit-transform: translate3d(150%,0,0);
        -moz-transform: translate3d(150%,0,0);
        -ms-transform: translate3d(150%,0,0);
        -o-transform: translate3d(150%,0,0);
        transition: transform 0.3s cubic-bezier(0.5,0.5,0.3,1)  ;
}
    & span{
        transition: transform 0.3s cubic-bezier(0.5,0.5,0.3,1)  ;
    }
    &:hover:before{
      transform: scale3d(1,1,1);
      -webkit-transform: scale3d(1,1,1);
      -moz-transform: scale3d(1,1,1);
      -ms-transform: scale3d(1,1,1);
      -o-transform: scale3d(1,1,1);
      transform-origin: left;
    }
    &:hover:after{
        transform: translate3d(0,0,0);
        -webkit-transform: translate3d(0,0,0);
        -moz-transform: translate3d(0,0,0);
        -ms-transform: translate3d(0,0,0);
        -o-transform: translate3d(0,0,0);
        color: $purple-electric;
   }
   &:hover span{
    transform: translate3d(-150,0,0);
    -webkit-transform: translate3d(-150,0,0);
    -moz-transform: translate3d(-150,0,0);
    -ms-transform: translate3d(-150,0,0);
    -o-transform: translate3d(-150,0,0);
}
}
.coolButton{
    position: relative;
    border: none;
    cursor: pointer;
    width: 175px;
    height: 120px;
    background: none;
    color: #fff;
    &:before{
        position: absolute;
        top:0;
        left:0;
        width: 100%;
        height: 100%;
    }
    &:before{
       content: "";
       background: #fff;
       clip-path: path("M154.5,88.5 C131,113.5 62.5,110 30,89.5 C-2.5,69 -3.5,42 4.5,25.5 C12.5,9 33.5,-6 85,3.5 C136.5,13 178,63.5 154.5,88.5 Z");
       transition:  clip-path 0.5s cubic-bezier(0.585,2.5,0.645,0.55) ;
       -webkit-transition:  clip-path 0.5s cubic-bezier(0.585,2.5,0.645,0.55) ;
       -moz-transition:  clip-path 0.5s cubic-bezier(0.585,2.5,0.645,0.55) ;
       -ms-transition:  clip-path 0.5s cubic-bezier(0.585,2.5,0.645,0.55) ;
       -o-transition:  clip-path 0.5s cubic-bezier(0.585,2.5,0.645,0.55) ;
}

    &:hover:before{
        
       
        
        clip-path: path("M143,77 C117,96 74,100.5 45.5,91.5 C17,82.5 -10.5,57 5.5,31.5 C21.5,6 79,-5.5 130.5,4 C182,13.5 169,58 143,77 Z");
        
}
& span{
    mix-blend-mode: difference;
    font-weight: 500;

    
}
&:after {
    content: "";
    position: absolute;
    top: 4px;
    left: 0;
    height: 85%;
    width: 95%;
    border: 1px solid #fff;
    border-radius: 58% 42% 55% 45% / 56% 45% 55% 44%;
    transform: rotate(-20deg);
    transition: transform 0.5s cubic-bezier(0.585,2.5,0.645,0.55) ;
    -webkit-transition: transform 0.5s cubic-bezier(0.585,2.5,0.645,0.55) ;
    -moz-transition: transform 0.5s cubic-bezier(0.585,2.5,0.645,0.55) ;
    -ms-transition: transform 0.5s cubic-bezier(0.585,2.5,0.645,0.55) ;
    -o-transition: transform 0.5s cubic-bezier(0.585,2.5,0.645,0.55) ;
    box-shadow: 0 0 10px rgba(204, 0, 255, 0.993);
}   
&:hover:after{
 transform: translate3d(0,-5px,0);
 -webkit-transform: translate3d(0,-5px,0);
 -moz-transform: translate3d(0,-5px,0);
 -ms-transform: translate3d(0,-5px,0);
 -o-transform: translate3d(0,-5px,0);
}
@media (max-width:800px) {
    &{
        transform: scale3d(0.7,0.7,0.7);
        -webkit-transform: scale3d(0.7,0.7,0.7);
        -moz-transform: scale3d(0.7,0.7,0.7);
        -ms-transform: scale3d(0.7,0.7,0.7);
        -o-transform: scale3d(0.7,0.7,0.7);
        font-size: 23px;
}
    
}

}
