@import "./base.scss";
.loading{
    position: absolute;
    top:0;
    left:0;
    bottom: 0;
    right: 0;
    height: 100vh;
    width: 100%;
    z-index: 99;
    background: black;
    //background: #0b134f;
    display: grid;
    place-items: center;
    font-family: "Orbitron", sans-serif;
   font-optical-sizing: auto;
   font-weight: 400;
   font-style: normal;
   
   
    &__box{
        position: relative;
        width: 500px;
        height: 200px;
        border: 3px solid $purple-electric;
        border-top: 3px solid $light-gold-color ;
        border-bottom: 3px solid $light-gold-color ;
        @media (max-width:600px) {
            &{
                width: 90vw;
            }
        }
       
    }
    &__bar{
        width: 90%;
        height: 10px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%);
        -webkit-transform: translateX(-50%);
        -moz-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        -o-transform: translateX(-50%);
        background: #ccc;
        border-radius: 2px;
        -webkit-border-radius: 2px;
        -moz-border-radius: 2px;
        -ms-border-radius: 2px;
        -o-border-radius: 2px;
        &--inner{
            height: 100%;
            width: 0;
            border-radius: 2px;
            -webkit-border-radius: 2px;
            -moz-border-radius: 2px;
            -ms-border-radius: 2px;
            -o-border-radius: 2px;
            background: $light-gold-color;
}

}
&__text{
    position: relative;
    color: #fff;
    padding: 1rem;
    font-size:20px ;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    @media (max-width:355px) {
        &{
            font-size: 17px;
        }
    }
    &--dot{
        width: 15px;
        height: 15px;
        margin: 0 3px ;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        background: #fff;
        animation: pulse infinite 1s;
        -webkit-animation: pulse infinite 1s;
        @keyframes pulse
{
            from{
                opacity: 0;
                background: $light-gold-color;


            }
            to{
                opacity: 1;

            }
        }
}
&--border{
    width:85%;
    height:1px;
    background: $light-gold-color;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%) ;
    -webkit-transform: translateX(-50%) ;
    -moz-transform: translateX(-50%) ;
    -ms-transform: translateX(-50%) ;
    -o-transform: translateX(-50%) ;
}

}
&__counter{ 
    position: absolute;
    top: 70%;
    left:0;
    color:#fff;
    font-size: 20px;
    font-weight: 700;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    &--number{
        color: $light-gold-color;
    }
}
&__width_animation{
    animation: width_animation 1s forwards;
    -webkit-animation: width_animation 1s forwards;
    @keyframes width_animation
{
        0%{
            width:85%
        }
        100% {
            width: 280px;
        }
        
    }

}
&__svg{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
    width: 85%;
    opacity: 0;
}
}